.welcomeButton {
  background-color: black;
  text-align: center;
  cursor: pointer;
  color: white;
  padding: 15px 32px;
  font-family: 'Karla', sans-serif;
  margin: auto;
  width: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.welcomeButton:hover {
  padding-top: 25px;
  padding-bottom: 25px;
  width: 100px;
}

.welcome {
  margin-top: 10%;
  width: 70%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Karla', sans-serif;
  font-size: 30px;
  font-weight: 400;
}

.stickyTopBar {
  font-family: 'Karla', sans-serif;
  font-size: 15px;
  line-height: 45px;
  font-weight: 700;
  color: white;
  position: fixed;
  top: 0;
  width: 130px;
  margin-left: 10px;
  margin-top: 10px;
  text-align: center;
  background-color: rgba(234, 62, 55, 0.9);
}

/* Add some top padding to the page content to prevent sudden quick movement (as the navigation bar gets a new position at the top of the page (position:fixed and top:0) */
.sticky + .content {
  padding-top: 60px;
}

